<template>
  <div class="servicos">
    <h1>Serviços</h1>
    <form class="busca">
      <label>OS: </label>
      <input align="center" type="text" id="procurar" name="procurar" placeholder="Digite o número da ordem de serviço" v-on:input="busca = $event.target.value">
    </form>
    <transition>
      <!-- <ul class="resultadoTotal">
        <li v-for="(servico, os) in servicos" :key="os">OS: {{servico.os}} - Título: {{servico.titulo}} - Descrição: {{servico.descricao}} - Data de Solicitação: {{servico.dataSolicitacao}} - Data de Visita: {{servico.dataVisita}} <button>Editar</button></li> 
      </ul> -->
      <ul class="resultadoTotal">
        <li v-for="(servico, os) in servicoBusca" :key="os">OS: {{servico.os}} - Título: {{servico.titulo}} - Descrição: {{servico.descricao}} - Data de Solicitação: {{servico.dataSolicitacao}} - Data de Visita: {{servico.dataVisita}} <button>Editar</button></li>
      </ul>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      servicos: [],
      busca: "",
    };
  },
  methods: {
    todosServicos() {
      fetch('http://localhost:3000/informacoes')
      .then(r => r.json())
      .then((r) => {
        this.servicos = r
      });
    }
  },
   computed: {
    servicoBusca() {
      if (this.busca) {
        let exp = new RegExp(this.busca.trim(), 'i');
        return this.servicos.filter(servico => exp.test(servico.os));
      }
      else return this.servicos;
    }
  },
  created() {
    this.todosServicos();
  }
}
</script>


